import React, { Component } from 'react';
import Car from './Car.js';
import Info from './Info.js';
import indexeddb from 'rtsightings/src/indexeddb.js';
import moment from 'moment';
import rtsightings from 'rtsightings';
import './App.css';

class App extends Component {

	constructor(props) {
		super(props);
		var cars = [
			'gray',
			'whiteSub',
			'greenSub',
			'whiteCoveredTruck',
			'whiteTruck',
			'redTruck',
			'redSedan'
		];
		this.state = {cars: cars, carsSighted: [], sightingDatetime: moment().format(), shown: 'info', notShown: 'hdn', show: false};
		this.setTheCar = this.setTheCar.bind(this);
		this.markSighting = this.markSighting.bind(this);
		this.doShow = this.doShow.bind(this);

	}	

	makeArrayUnique(carsSighted) {
		function onlyUnique(value, index, self) {
			return self.indexOf(value) === index;
		}
		var uniqueCars = carsSighted.filter(onlyUnique);
		return uniqueCars;
	}

	sortArray(uniqueCars) { 
		uniqueCars.sort(function(a, b){
			if(a < b) { return -1; }
			if(a > b) { return 1; }
			return 0;
		});
		return uniqueCars;
	}

	setTheCar(event) {
		var theCar = event.target.id;
		var carsSighted = this.state.carsSighted;
		var alreadyThere = carsSighted.indexOf(theCar);
		if (alreadyThere > -1) {
			carsSighted = carsSighted.filter(function(e) { return e !== theCar });
		} else {
			carsSighted.push(theCar);
		}
		var uniqueCars = this.makeArrayUnique(carsSighted);
		var sortedCars = this.sortArray(uniqueCars);
		this.setState({carsSighted: sortedCars});
	}

	markSighting(event) {
		var sightingName = 'sighting' + moment().format('YYMMDDkkmm');
		var result = rtsightings(sightingName, this.state.sightingDatetime, this.state.carsSighted, 'sightings', indexeddb);
console.log(result);
		this.setState({carsSighted: []});
//		rtsightings.get();
	}

	doShow(event) {
		var showIt = !(this.state.show);
		if (showIt) {
			this.setState({shown: 'hdn', notShown: 'info', show: showIt});
		} else {
			this.setState({shown: 'info', notShown: 'hdn', show: showIt});
		}
	}


	render() {
		return (
			<div className="App">
				<header className="App-header">
				<p>
					Redtruck Sightings<Info shown={this.state.shown} notShown={this.state.notShown} doShow={this.doShow}  />
				</p>
				{this.state.cars.map((carName, i) => {
					var selctd = this.state.carsSighted.indexOf(carName) > -1 ? 'selectedCar' : 'car';
					return (<Car key={i} carName={carName} selctd={selctd} setCar={this.setTheCar} />)
				})}
				<button onClick={this.markSighting}>
					Save
				</button>
				</header>
			</div>
		);
	}
}

export default App;
