import React, { Component } from 'react';
import './App.css';

class Info extends Component {

	render() {
		return (
			<span className="infoContainer" onClick={this.props.doShow}>
				<span className={this.props.shown}>
					[i]
				</span>
				<span className={this.props.notShown}>
					STUFF
				</span>
			</span>
		);
	}
}

export default Info;
